.App {
  text-align: center;
  width: 100vw;
  font-size: 62.5%;
  overflow: hidden;
}

a:link {
  color: #10AAB4;
}

a:visited {
  color: #323232;
}

a:hover {
  border-radius: 5px;
  background-color: #323232;
  color: #FFC700;
}

a:active {
  border-radius: 5px;
  background-color: #323232;
  color: #10AAB4;
}

a:focus {
  border: 0px solid #d10000;
  border-radius: 5px;
  background-color: #323232;
  color: #FFC700;
}
